* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Oswald", sans-serif;
  }
  body {
	height: 100vh;
	width: 100vw;
	background:linear-gradient(to right,#925AFF , #1976d2);
  }
  
  .emotions {
	width: 350px;
	height: 500px;
	background: #fff;
	border-radius: 5px;
	text-align: center;
	padding: 20px;
	display: flex;
	flex-direction: column;
	gap: 20px;
  }
  
  .navbar {
	  width: 100%;
	  height: 70px;
	  background-color: #925AFF;
	  display: flex;
	  align-items: center;
	  border-style: solid;
	  border-width: 0 0 1px 0;
	  justify-content: space-between;
  }
  
  .navbar h1 {
	  color: white;
	  font-size: 25px;
	  margin-left: 50px;
  }
  .videoplayer{
	  width: 100%;
	  height: 500px;
  }
  .button{
	  display: flex;
  }
  .white_btn {
	  border: none;
	  outline: none;
	  padding: 12px 0;
	  background-color: white;
	  border-radius: 20px;
	  width: 120px;
	  font-weight: bold;
	  font-size: 14px;
	  cursor: pointer;
	  margin-right: 20px;
  }
  
  @media only screen and (max-width: 600px) {
	.mainBody {
	 flex-direction: column;
	}
	/* .videoplayer{
		width: 100%;
		height:40% ;
	} */
	.button{
		display: flex;
		align-content: center;
		flex-direction: column;
	}
	.emotions{
		height: 100%;
		margin-bottom: 10%;
	}
  }
  